import { ProviderInfoEditComponent } from './../common/provider-info-edit/provider-info-edit.component';
import { EnvService } from "src/app/services/env.service";
import { Provider } from "src/app/models/provider";
import { ProviderService } from "src/app/services/provider.service";
import { UserService } from "src/app/services/user.service";
import { AdditionalInfo } from "./../models/additional-info";
import { AdditionalInfoEditComponent } from "./../common/additional-info-edit/additional-info-edit.component";
import { AlertService } from "./../services/alert.service";
import { TestBed } from "@angular/core/testing";
import { ChangePasswordComponent } from "./../common/change-password/change-password.component";
import { FileHelperService } from "./../services/file-helper.service";
import {
  FileModel,
  FileFetchStatus,
  FileUploadStatus,
} from "./../models/file-model";
import { Subscription, Subject, from, of } from "rxjs";
import { AuthService } from "src/app/services/auth.service";
import { User } from "./../models/user";
import {
  MenuController,
  ModalController,
  PopoverController,
  ActionSheetController,
  IonTextarea,
} from "@ionic/angular";
import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { concatMap, catchError, take } from "rxjs/operators";
import { ViewerModalComponent } from 'ngx-ionic-image-viewer';

@Component({
  selector: "app-profile",
  templateUrl: "./profile.page.html",
  styleUrls: ["./profile.page.scss"],
})
export class ProfilePage implements OnInit {
  @ViewChild("attachment", { static: false, read: ElementRef })
  attachment: ElementRef;
  user: User = null;
  userSubject: Subject<User> = new Subject<User>();
  getName = User.getName;
  userFetchedSubscription: Subscription;
  roleName = null;
  avatarFile: FileModel;
  certificateFile: FileModel;
  provider: Provider = null;
  verfiedText = null;
  editingAboutMe = false;
  constructor(
    private menu: MenuController,
    private auth: AuthService,
    private fileHelper: FileHelperService,
    private modalController: ModalController,
    private alert: AlertService,
    private providerService: ProviderService,
    private env: EnvService,
    private actionSheet: ActionSheetController
  ) {}
  loadProvider() {
    this.auth.getUser().then((user) => {
      this.env.somethingIsPending("getting provider info");
      this.providerService
        .getProviderByUserId(user.id)
        .pipe(
          catchError((error) => {
            this.alert.presentError("دریافت اطلاعات مشاور با مشکل روبرو شد.");
            return of(false);
          })
        )
        .subscribe((result) => {
          this.env.somethingIsDonePending("getting provider info");

          if (result) {
            this.provider = result as Provider;
            
            let cert = Provider.getCertificateFileName(this.provider);
            if (cert) {
              this.provider.certificateFile = new FileModel();
              this.provider.certificateFile.name = cert;
              this.providerService
                .downloadCertificate(this.provider)
                .subscribe();
            }
            this.verfiedText = Provider.getVerifiedByAdminText(this.provider);
            if (!this.provider.user.additional_info) {
              let additionalInfo = new AdditionalInfo();
              additionalInfo.postal_code = "";
              additionalInfo.national_code = "";
              additionalInfo.land_line_number = "";
              additionalInfo.address = "";
              this.provider.user.additional_info = additionalInfo;
            }
          }
        });
    });
  }
  ngOnInit() {
    this.auth.isProvider().then((isProvider) => {
      this.loadProvider();
    });
  }
  ionViewWillEnter() {
    this.menu.enable(true);
    this.userFetchedSubscription = this.auth.fetchedUser.subscribe((result) => {
      if (result) {
        this.auth.getUser().then((result) => {
          this.user = result;
          this.userSubject.next(result);
          this.roleName = User.getRoleName(result);
        });
      }
    });
    this.auth.getUserRemote().subscribe();
  }
  isAvatarUploading() {
    return (
      this.avatarFile != null &&
      this.avatarFile.upload_status ==
        FileUploadStatus.FILE_UPLOAD_STATUS_UPLOADING
    );
  }
  isCertificateUploading() {
    return (
      this.certificateFile != null &&
      this.certificateFile.upload_status ==
        FileUploadStatus.FILE_UPLOAD_STATUS_UPLOADING
    );
  }
  isCertificateDownloading()
  {
    return this.provider.certificateFile.fetch_status == FileFetchStatus.FILE_FETCH_STATUS_FETCHING;
  }
  ionViewWillLeave() {
    this.userFetchedSubscription.unsubscribe();
  }
  avatarUploadProgress() {
    return (this.avatarFile.upload_progress / 100).toPrecision(2);
  }
  certificateUploadProgress() {
    return (this.certificateFile.upload_progress / 100).toPrecision(2);
  }
  certificateDownloadProgress() {
    return (this.provider.certificateFile.fetch_progress / 100).toPrecision(2);
  }
  handleFile(files) {
    this.avatarFile = new FileModel();
    this.avatarFile.fetch_status =
      FileFetchStatus.FILE_FETCH_STATUS_SUCCESS;
    this.avatarFile.upload_status =
      FileUploadStatus.FILE_UPLOAD_STATUS_UPLOADING;
    this.avatarFile.content = files[0];
    this.avatarFile.size = files[0].size;
    if (this.avatarFile.size <= 2000000) {
      this.fileHelper.processImageProperties(this.avatarFile).subscribe(
        (result) => {
          this.avatarFile = result;
          this.fileHelper
            .uploadUserAvatar(this.avatarFile)
            .subscribe(
              (result) => {
                this.avatarFile.upload_status =
                  FileUploadStatus.FILE_UPLOAD_STATUS_SUCCESS;
                this.auth.getUserRemote().subscribe();
                this.alert.presentSuccess("عکس پروفایل آپلود شد.");
              },
              (error) => {
                this.avatarFile.upload_status =
                  FileUploadStatus.FILE_UPLOAD_STATUS_FAIL;
                console.error("file upload error");
              }
            );
        },
        (error) => {
          console.error(error);
          this.avatarFile = null;
        }
      );
    } else {
      this.alert.presentError("حداکثر سایز مجاز: ۲ مگابایت");
      this.avatarFile = null;
    }
  }
  uploadCertificate(files) {
    this.certificateFile = new FileModel();
    this.certificateFile.fetch_status =
      FileFetchStatus.FILE_FETCH_STATUS_SUCCESS;
    this.certificateFile.upload_status =
      FileUploadStatus.FILE_UPLOAD_STATUS_UPLOADING;
    this.certificateFile.content = files[0];
    this.certificateFile.size = files[0].size;
    if (this.certificateFile.size <= 5000000) {
      this.fileHelper.processImageProperties(this.certificateFile).subscribe(
        (result) => {
          this.certificateFile = result;
          this.providerService
            .uploadCertificate(this.certificateFile)
            .subscribe(
              (result) => {
                this.certificateFile.upload_status =
                  FileUploadStatus.FILE_UPLOAD_STATUS_SUCCESS;
                this.auth.getUserRemote().subscribe();
                this.alert.presentSuccess("مدرک تحصیلی آپلود شد.");
                this.loadProvider();
              },
              (error) => {
                this.certificateFile.upload_status =
                  FileUploadStatus.FILE_UPLOAD_STATUS_FAIL;
                console.error("file upload error");
              }
            );
        },
        (error) => {
          console.error(error);
          this.certificateFile = null;
        }
      );
    } else {
      this.alert.presentError("حداکثر سایز مجاز: ۵ مگابایت");
      this.certificateFile = null;
    }
  }
  openChangePassword() {
    this.presentChangePasswordModal().subscribe();
  }
  showSettings() {
    this.presentSettings().subscribe();
  }
  presentChangePasswordModal() {
    return from(
      this.modalController.create({
        component: ChangePasswordComponent,
        componentProps: {},
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
  openProviderInfo()
  {
    this.presentProviderInfoModal().subscribe();
  }
  presentProviderInfoModal()
  {
    return from(
      this.modalController.create({
        component: ProviderInfoEditComponent,
        componentProps: {
          provider: this.provider,
          editable: !this.provider.verified_by_admin,
        },
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {
          if (data["data"]) {
            if (data["data"]) {
              this.provider.user.additional_info = data["data"];
            }
          }
        });
        return from(modal.present());
      })
    );
  }
  openAdditionalInfoEdit() {
    this.presentAdditionalInfoModal().subscribe();
  }
  presentAdditionalInfoModal() {
    return from(
      this.modalController.create({
        component: AdditionalInfoEditComponent,
        componentProps: {
          additionalInfo: this.provider.user.additional_info,
          editable: !this.provider.verified_by_admin,
        },
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {
          if (data["data"]) {
            if (data["data"]) {
              this.provider.user.additional_info = data["data"];
            }
          }
        });
        return from(modal.present());
      })
    );
  }
  
  presentSettings() {
    return from(
      this.actionSheet.create({
        mode: "ios",
        buttons: [
          {
            text: "تغییر گذرواژه",
            role: "destructive",
            icon: "finger-print-outline",
            handler: () => {
              this.openChangePassword();
            },
          },
          {
            text: "آپلود عکس پروفایل",
            icon: "camera-outline",
            handler: () => {
              this.attachment.nativeElement.click();
            },
          },
          {
            text: "انصراف",
            role: "cancel",
            handler: () => {},
          },
        ],
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {
          if (data["data"]) {
            if (data["data"]) {
              this.provider.user.additional_info = data["data"];
            }
          }
        });
        return from(modal.present());
      })
    );
  }
  editAboutMe(textArea:IonTextarea)
  {
    if(this.editingAboutMe)
    {
      this.editingAboutMe = false;
      this.provider.about_me = textArea.value;
      this.providerService.updateAboutMe(this.provider).pipe(take(1)).subscribe();
    }
    else
    {
      textArea.setFocus();
      this.editingAboutMe = true;
    }
  }

}
